import React from 'react'
import { useParams } from 'react-router-dom'
import { getAnimal } from '../data/dataFetcher';
import { Animal } from '../models/Animal';
import Carousel from 'react-material-ui-carousel';


const AnimalPage: React.FC = () => {
  const id: string | undefined = useParams().id;
  if (id === undefined) return (<h1>not found</h1>)
  const animal: Animal | undefined = getAnimal(id);

  return (
    <>
      <h1>{animal?.commonName}</h1>
      <Carousel height={'200px'} autoPlay={false}>
        {animal?.images.map((imageUrl) => (
          <img
            height='200px'
            src={imageUrl}
            alt={animal?.commonName}
          />
        ))}
      </Carousel>
      <p><i>{animal?.scientificName}</i></p>
      <p><b>Distribution:</b> {animal?.distribution}</p>
      <p><b>Habitat:</b> {animal?.habitat}</p>
      <p><b>Conservation Status:</b> {animal?.conservationStatus}</p>
      <h2>Quick Facts</h2>
      <ul>
        {
          animal?.quickFacts?.map((fact) => (<li>{fact}</li>))
        }
      </ul>
      <h2>Description</h2>
      <p>{animal?.description}</p>
    </>

  )
}

export default AnimalPage