import React from 'react'
import Card from '@mui/material/Card';
import { Animal } from '../models/Animal'
import { CardActionArea, CardContent, CardMedia, Typography } from '@mui/material';
import Grid from '@mui/material/Unstable_Grid2';
import { Link as RouterLink } from 'react-router-dom';

interface SearchResultProps {
  animal: Animal
};

const SearchResult: React.FC<SearchResultProps> = (props) => {
  return (
    <Card >
      <CardActionArea component={RouterLink} state={{animal: props.animal}} to={`/animal/${props.animal.id}`}>
        <Grid container spacing={2}>
          <Grid xs={4}>
            <CardMedia
              component="img"
              height="140"
              image={props.animal.images[0]}
              alt={props.animal.commonName}
              sx={{ objectFit: "contain" }}
            />
          </Grid>
          <Grid xs={8}>
            <CardContent>
              <Typography gutterBottom variant="h5" component="div">
                {props.animal.commonName}
              </Typography>
              <Typography variant="body2" color="text.secondary">
                Distribution: {props.animal.distribution}
              </Typography>
            </CardContent>
          </Grid>
        </Grid>
      </CardActionArea>
    </Card>
  )
}

export default SearchResult